/*
* Buttons
* ================ */
.btn {
  display: inline-block;
  text-align: center;
  line-height: inherit;
  white-space: nowrap;
  max-width: 100%;
  text-overflow: ellipsis;
  cursor: pointer;
  vertical-align: middle;
  position: relative;
  z-index: 1;
  text-decoration: none;
  text-transform: uppercase;
  margin: 10px;
}

.brk-bordered-theme .btn {
  border-radius: var(--b-radius);
}

.btn-shadow {
  box-shadow: 0 5px 16px var(--brk-base-1) !important;
}

.btn-dark-shadow {
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.4) !important;
}

.btn-no-shadow {
  box-shadow: none !important;
}

.invert,
.invert .bsk-btn {
  color: #fff !important;
}

.btn-min-width-200 {
  min-width: 200px;
}

@media screen and (max-width: 767px) {
  .btn-xs-max-width {
    width: 100%;
  }
}

/* SIZES BTN */
.btn-sm {
  padding: 4px 22px;
  font-size: 13px;
}

.btn-sm i.icon-inside {
  font-size: 13px;
}

.btn-sm.btn-inside-out > .before {
  padding-top: 4px;
}

.btn-sm.btn-simple {
  min-width: 130px;
}

.btn-sm-1 {
  padding: 8px 26px;
  font-size: 13px;
}

.btn-sm-1 i.icon-inside {
  font-size: 13px;
}

.btn-sm-1.btn-inside-out > .before {
  padding-top: 8px;
}

.btn-sm-1.btn-simple {
  min-width: 130px;
}

.btn-md {
  padding: 10px 33px;
  font-size: 14px;
}

.btn-md i.icon-inside {
  font-size: 14px;
}

.btn-md.btn-inside-out > .before {
  padding-top: 10px;
}

.btn-md-1 {
  padding: 10px 33px;
  letter-spacing: 3.5px;
  min-height: 44px;
  font-size: 14px;
}

.btn-md-1 i.icon-inside {
  font-size: 14px;
}

.btn-md-1.btn-inside-out > .before {
  padding-top: 10px;
}

.btn-md-2 {
  padding: 10px 33px;
  font-size: 16px;
}

.btn-md-2 i.icon-inside {
  font-size: 16px;
}

.btn-md-2.btn-inside-out > .before {
  padding-top: 12px;
}

.btn-lg {
  padding: 15px 33px;
  font-size: 16px;
}

.btn-lg i.icon-inside {
  font-size: 16px;
}

.btn-lg.btn-inside-out > .before {
  padding-top: 15px;
}

.btn-xl {
  padding: 17px 42px;
  font-size: 19px;
}

.btn-xl i.icon-inside {
  font-size: 19px;
}

.btn-xl.btn-inside-out > .before {
  padding-top: 17px;
}

/* SIZES BTN */
.btn__full_width {
  width: 100%;
}

.btn [class*="fa-"].icon-left {
  margin: 0 0 0 10px;
  position: relative;
  z-index: 3;
}

.border-radius-50 {
