}

.brk-bordered-theme .btn {
  border-radius: var(--b-radius);
}

.brk-bordered-theme .btn.btn-slice span {
  border-radius: var(--b-radius);
}

.brk-bordered-theme .btn.btn-prime .border-btn {
  border-radius: var(--b-radius);
}

.brk-bordered-theme .btn.btn-lightning span.bg {
  border-radius: var(--b-radius);
}

.brk-bordered-theme .btn.btn-ice .left-border, .brk-bordered-theme .btn.btn-ice .btn-text {
  border-radius: var(--b-radius);
}

.brk-bordered-theme .btn.btn-two-bg .before, .brk-bordered-theme .btn.btn-two-bg .after {
  border-radius: var(--b-radius);
}

.btn:focus {
  box-shadow: none;
}

.btn [class*="fa-"] {
  margin-right: 10px;
  vertical-align: baseline;
}

[dir="rtl"] .btn [class*="fa-"] {
  margin-right: 0;
  margin-left: 10px;
}

.btn-icon:before, .btn-icon:after {
  padding-left: 10px;
}

.btn-icon [class*="fa-"] {
  position: relative;
  color: #fff;
  font-size: 16px;
  z-index: 1;
}

.btn-icon-right i {
  position: absolute !important;
  right: 20px;
  margin-right: 0 !important;
  top: 50%;
  transform: translateY(-50%);
}

.btn-icon-right:before, .btn-icon-right:after {
  padding-left: 0;
}

.btn.btn-icon-abs [class*="fa-"] {
  position: absolute;
  top: 50%;
  left: -16px;
  transform: translateY(-50%);
  font-size: 48px;
  color: #e0e0ff;
  opacity: .3;
  z-index: 1;
}

.btn-simple {
  font-size: 24px;
  position: relative;
  background: #fff;
  color: var(--brand-primary);
  box-shadow: 0 5px 16px 0 rgba(0, 0, 0, 0.2);
  padding: 10px 15px;
  min-width: 110px;
  vertical-align: middle;
  line-height: 16px;
}

.btn-simple:hover {
  color: #fff !important;
  background: var(--brand-primary);
}

.btn-simple i.icon-inside {
  font-size: 24px;
}

.btn-simple [class*="fa-"]:not(.icon-inside) {
  margin-right: 0;
  font-size: 24px;
  font-weight: 700;
}

.btn-simple.btn-circle {
  min-width: 0;
  width: 52px;
  height: 52px;
  color: var(--brand-primary) !important;
}

.btn-simple.btn-circle > .before, .btn-simple.btn-circle > .after {
  display: none;
}

.btn-simple.btn-circle:hover {
  background: var(--brk-base-4) !important;
}

.btn-simple.btn-circle:hover .text {
  color: var(--brand-primary) !important;
}

.btn-simple.btn-circle i.icon-inside {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

[dir="rtl"] .btn-simple.btn-circle i.icon-inside {
  margin-left: 0;
}

.btn-simple.btn-circle i.icon-inside:before {
  line-height: 24px;
  vertical-align: top;
}

.btn-inside-out {
  overflow: hidden;
  border: none;
  transition: background .4s;
  transition-delay: .3s;
  will-change: background;
}

.btn-inside-out_dark {
  background: #272727 !important;
}

.btn-inside-out_dark:hover {
  background: var(--brk-base-4) !important;
  color: #272727;
}

.btn-inside-out_dark > .before,
.btn-inside-out_dark > .after {
  background: #272727 !important;
}

.btn-inside-out .icon-inside {
  margin-right: 5px;
}

[dir="rtl"] .btn-inside-out .icon-inside {
  margin-right: 0;
  margin-left: 5px;
}

.btn-inside-out .icon-inside ~ .before, .btn-inside-out .icon-inside ~ .after {
  padding-left: 24px;
}

[dir="rtl"] .btn-inside-out .icon-inside ~ .before, [dir="rtl"] .btn-inside-out .icon-inside ~ .after {
  padding-left: 0;
  padding-right: 24px;
}

.btn-inside-out > .before, .btn-inside-out > .after {
  position: absolute;
  width: 100%;
  height: 50%;
  left: 0;
  overflow: hidden;
  background: var(--brand-primary);
  color: #fff;
  transition: \transform 0.3s;
}

.btn-inside-out > .before {
  top: 0;
  padding-top: 8px;
}

.btn-inside-out > .after {
  bottom: 0;
  line-height: 0;
}

.btn-inside-out > span.text {
  display: inline-block;
  transform: scale(0.1);
  opacity: 0;
  transition: \transform 0.3s, opacity 0.3s;
}

.btn-inside-out:hover {
  background: var(--secondary);
  transition: background .4s;
  transition-delay: 0s;
  color: #fff;
  box-shadow: 0 5px 10px -2px var(--secondary);
}

.btn-inside-out:hover > .before {
  transform: translateY(-105%);
}

.btn-inside-out:hover > .after {
  transform: translateY(105%);
}

.btn-inside-out:hover > span.text {
  opacity: 1;
  transform: scale(1);
}

.btn-inside-out-invert {
  border: none;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.3);
}

.btn-inside-out-invert > .before, .btn-inside-out-invert > .after {
  background: #fff;
  color: var(--brand-primary);
}

.btn-inside-out-invert:hover.btn-icon [class*="fa-"]:before,
.btn-inside-out-invert:hover span.text {
  color: #fff;
}

.btn-inside-out-invert span.text {
  color: var(--brand-primary);
}

.btn-inside-out-invert.btn-icon [class*="fa-"]:before {
  color: var(--brand-primary);
}

.btn-inside-out-invert-light {
  border: none;
  box-shadow: 0 5px 21px var(--brk-base-1);
}

.btn-inside-out-invert-light > .before, .btn-inside-out-invert-light > .after {
  background: #fff;
  color: var(--brand-primary);
}

.btn-inside-out-invert-light:hover {
  background-color: rgba(255, 255, 255, 0.9);
}

.btn-inside-out-invert-light:hover.btn-icon [class*="fa-"]:before {
  color: var(--brand-primary);
}

.btn-inside-out-invert-light span.text {
  color: var(--brand-primary);
}

.btn-inside-out-invert-light.btn-icon [class*="fa-"]:before {
  color: var(--brand-primary);
}

.btn-inside-out-light-hover:hover {
  background-color: #ffffff;
  color: #222222;
}

.btn-prime {
  color: var(--brand-primary);
  background: #fff;
  border: none;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn-prime .border-btn {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid var(--brand-primary);
  z-index: -1;
}

.btn-prime > .before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 0;
  background: var(--brand-primary);
  transition: background .4s, height .4s;
  z-index: -1;
}

.btn-prime > .after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0;
  background: var(--brand-primary);
  transition: background .4s, height .4s;
  z-index: -1;
}

.btn-prime:hover {
  color: var(--brand-primary) !important;
}

.btn-prime:hover > .before,
.btn-prime:hover > .after {
  height: 50%;
  background: var(--brk-base-4) !important;
}

.btn-prime-white > .before, .btn-prime-white > .after {
  background: #fff;
}

.btn-prime-white .border-btn {
  border-color: #fff;
}

.btn-prime-white:hover {
  color: var(--brand-primary) !important;
}

.btn-prime-white-transparent {
  background: transparent;
  color: #fff;
}

.btn-prime-white-transparent:hover > .before,
.btn-prime-white-transparent:hover > .after {
  background: #fff !important;
}

.btn-prime-white-transparent .border-btn {
  border-color: #fff;
}

.btn-prime.btn-invert {
  color: #fff;
}

.btn-prime.btn-invert > .before, .btn-prime.btn-invert > .after {
  background: transparent;
}

.btn-prime.btn-invert .border-btn {
  border-color: var(--brk-base-2);
}

.all-light .btn-prime {
  background: transparent;
  color: #fff;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.3);
}

.all-light .btn-prime:hover [class*="fa-"].icon-left {
  color: #fff;
}

.all-light .btn-prime [class*="fa-"].icon-left {
  color: var(--brand-primary);
  font-size: 24px;
  vertical-align: sub;
  float: right;
  line-height: 1;
  margin: -2px 6px 0 0;
}

.btn-gradient {
  background: var(--brand-primary);
  overflow: hidden;
  position: relative;
  appearance: none;
  border: none;
  color: #fff;
  --x:190px;
  --y:30px;
  --size: 200px;
  transition: background .3s;
}

.btn-gradient span,
.btn-gradient i {
  z-index: 1;
  position: relative;
  pointer-events: none;
}

.btn-gradient::before {
  content: '';
  position: absolute;
  left: var(--x);
  top: var(--y);
  width: var(--size);
  height: var(--size);
  background: radial-gradient(circle closest-side, var(--secondary), transparent);
  transform: translate(-50%, -50%);
  transition: width .2s, height .2s;
  z-index: 0;
}

.btn-gradient:hover {
  color: #ffffff;
}

.btn-gradient:hover::before {
  --size: 200px;
}

.btn-slice {
  background: #fff;
  overflow: hidden;
  position: relative;
  z-index: 1;
  border: none;
  color: var(--brand-primary);
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.5);
}

.btn-slice span {
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  border: 1px solid var(--brand-primary);
}

.btn-slice .border-btn {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid var(--brand-primary);
}

.btn-slice > .before, .btn-slice > .after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border: 0 solid;
  transform: rotate(360deg);
  transition: border-width .4s;
  z-index: -1;
}

.btn-slice > .before {
  bottom: 0;
  left: 0;
  border-color: transparent transparent transparent var(--brand-primary);
}

.btn-slice > .after {
  top: 0;
  right: 0;
  border-color: transparent var(--brand-primary) transparent transparent;
}

.btn-slice:hover {
  color: #fff !important;
}

.btn-slice:hover > .before, .btn-slice:hover > .after {
  border-width: 54px 220px;
}

.btn-pos {
  color: #333;
  background: #F6F6F6;
  overflow: hidden;
  position: relative;
}

.btn-pos span {
  position: absolute;
  left: 0;
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: var(--brand-primary);
  transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.btn-pos:hover {
  color: #fff;
}

.btn-pos:hover span {
  width: 250%;
  height: 250%;
}

.btn-lightning {
  overflow: hidden;
  position: relative;
  padding: 2px 14px;
}

.btn-lightning.btn-icon-abs [class*="fa-"] {
  z-index: 10;
}

.btn-lightning span.bg {
  background: #fff;
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  border-radius: 22px;
  z-index: 2;
}

.btn-lightning span:not(.bg) {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  z-index: 4;
  padding: 16px 0;
  height: 100%;
  font-size: 16px;
}

.btn-lightning > .before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: linear-gradient(to right, var(--brk-base-2), var(--brand-primary), var(--secondary));
}

.btn-lightning:after {
  background: #1d1d1d;
  content: "";
  height: 135px;
  left: -100px;
  opacity: .1;
  position: absolute;
  bottom: -40px;
  transform: rotate(30deg);
  transition: all 1s;
  width: 50px;
  transform-origin: bottom right;
  z-index: 3;
}

.btn-lightning:hover {
  color: #333;
}

.btn-lightning:hover:after {
  animation: lightning 2s infinite;
}

.btn-gradient-wave {
  color: #fff;
  background: linear-gradient(45deg, var(--text-base-color-2) 0%, var(--brk-secondary-3) 100%);
  border: none;
  overflow: hidden;
}

.btn-gradient-wave:before {
  content: '';
  position: absolute;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="1600" height="198"><path fill="#07e3ff" d="M.005 121C311 121 409.898-.25 811 0c400 0 500 121 789 121v77H0s.005-48 .005-77z" transform="matrix(-1 0 0 1 1600 0)"/></svg>');
  background-position: left bottom;
  background-repeat: repeat-x;
  background-size: contain;
  top: 70%;
  left: 0;
  right: 0;
  bottom: 0;
  animation: wave 60s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
  animation-play-state: paused;
}

.btn-gradient-wave:focus {
  color: #fff;
  background: linear-gradient(45deg, var(--text-base-color-2) 0%, var(--brk-secondary-3) 100%);
}

.btn-gradient-wave:hover {
  color: #fff !important;
}

.btn-gradient-wave:hover:before {
  animation-play-state: running;
}

.btn-ice {
  height: auto;
  border: none;
  color: var(--brand-primary);
  background-color: var(--brk-base-4);
  box-shadow: 0 5px 21px rgba(0, 0, 0, 0.5);
  padding: 0;
}

.btn-ice span.btn-text {
  position: relative;
  z-index: 2;
  color: var(--brand-primary);
  background: #fff;
  display: block;
  padding: 17px 16px;
  margin: 2px;
  border-radius: 23px;
  transition: color .4s;
  overflow: hidden;
}

.btn-ice span.btn-text > .before {
  display: block;
  position: absolute;
  width: 0;
  height: 100%;
  top: 0;
  left: -10%;
  z-index: -12;
  background: var(--brand-primary);
  transform: skewX(-20deg);
  transition: opacity .7s, width .7s;
  opacity: 1;
}

.btn-ice span.btn-text:after {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 100%;
  top: 0;
  left: -10%;
  z-index: -15;
  background: var(--brk-base-4);
  transform: skewX(-20deg);
  transition: opacity .4s, width .4s;
  opacity: 0;
}

.btn-ice:hover {
  color: #fff !important;
  background: transparent;
  box-shadow: none;
}

.btn-ice:hover span.btn-text {
  color: #fff !important;
}

.btn-ice:hover span.btn-text > .before {
  opacity: 1;
  width: 116%;
}

.btn-ice:hover span.btn-text:after {
  opacity: 1;
  width: 120%;
}

.btn-ice:hover .left-border, .btn-ice:hover .top-line {
  opacity: 0;
}

.btn-ice .left-border {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  right: 50%;
  bottom: 0;
  z-index: 1;
  background-color: var(--brand-primary);
  border-bottom-right-radius: 0 !important;
  border-top-right-radius: 0 !important;
  border-radius: 25px;
  transition: opacity .4s;
}

.btn-ice .top-line {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 50%;
  right: 25%;
  width: 25%;
  height: 2px;
  background-color: var(--brand-primary);
  transition: opacity .4s;
}

.btn-ice .top-line > .after {
  content: '';
  position: absolute;
  top: 0;
  right: -2px;
  height: 2px;
  width: 2px;
  z-index: 3;
  box-shadow: 1px 1px 3px var(--brand-primary), -1px -1px 3px var(--brand-primary), -1px 1px 3px var(--brand-primary), 1px -1px 3px var(--brand-primary);
}

.btn-two-bg {
  color: #fff;
  border: none;
  background-color: transparent;
}

.btn-two-bg span {
  position: relative;
  display: block;
  z-index: 3;
}

.btn-two-bg .before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background: linear-gradient(to right, rgba(var(--brk-base-2-rgb), 0.82), rgba(var(--brk-base-5-rgb), 0.82));
}

.btn-two-bg .after {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(to right, var(--brand-primary), var(--secondary));
  transition: \transform .4s;
}

.btn-two-bg:hover, .btn-two-bg:focus {
  color: #fff !important;
}

.btn-two-bg:hover .after {
  transform: translate(-10px, -10px);
}

.icon__btn {
  display: inline-block;
  margin: 10px;
  border-radius: 50%;
  color: #fff;
  background: var(--brand-primary);
  position: relative;
  z-index: 3;
}

.brk-bordered-theme .icon__btn {
  border-radius: var(--b-radius);
}

.icon__btn .spike {
  position: absolute;
  top: 8px;
  left: 8px;
  right: 8px;
  bottom: 8px;
  background: var(--brand-primary);
  z-index: -1;
}

.icon__btn .spike:nth-child(1) {
  transform: rotate(0deg);
}

.icon__btn .spike:nth-child(2) {
  transform: rotate(10deg);
}

.icon__btn .spike:nth-child(3) {
  transform: rotate(20deg);
}

.icon__btn .spike:nth-child(4) {
  transform: rotate(30deg);
}

.icon__btn .spike:nth-child(5) {
  transform: rotate(40deg);
}

.icon__btn .spike:nth-child(6) {
  transform: rotate(50deg);
}

.icon__btn .spike:nth-child(7) {
  transform: rotate(60deg);
}

.icon__btn .spike:nth-child(8) {
  transform: rotate(70deg);
}

.icon__btn .spike:nth-child(9) {
  transform: rotate(80deg);
}

.icon__btn .spike:nth-child(10) {
  transform: rotate(90deg);
}

.icon__btn > .before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(1.35);
  opacity: .1;
  background-color: var(--brand-primary);
  border-radius: 50%;
  z-index: -1;
}

.brk-bordered-theme .icon__btn > .before {
  border-radius: var(--b-radius);
}

.icon__btn > .after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: .2;
  background-color: var(--brand-primary);
  border-radius: 50%;
  z-index: -1;
}

.brk-bordered-theme .icon__btn > .after {
  border-radius: var(--b-radius);
}

.icon__btn_clear > .before {
  transform: scale(1);
}

.icon__btn:hover {
  color: #fff;
}

.icon__btn:hover > .before {
  animation: pulse 1.5s infinite;
  animation-delay: 0.3s;
}

.icon__btn:hover > .after {
  animation: pulse 1.5s infinite;
}

.icon__btn-circled > .before {
  top: -33px;
  left: -33px;
  right: -33px;
  bottom: -33px;
  transform: scale(1);
}

.icon__btn-circled > .after {
  top: -16px;
  left: -16px;
  right: -16px;
  bottom: -16px;
}

.icon__btn-circled:hover > .before {
  animation: pulse-paused 1s ease-in infinite;
  animation-delay: 0.2s;
}

.icon__btn-circled:hover > .after {
  animation: pulse-paused 1s ease-in infinite;
}

.icon__btn i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.icon__btn-invert {
  background: #fff;
  color: var(--brand-primary);
}

.icon__btn-invert > .bg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 100%;
  background: #fff;
}

.brk-bordered-theme .icon__btn-invert > .bg {
  border-radius: var(--b-radius);
}

.icon__btn-invert:hover {
  background: #fff;
  color: var(--brand-primary);
}

.icon__btn-white {
  background: #fff;
  color: var(--brand-primary);
}

.icon__btn-white > .before {
  background-color: #fff;
}

.icon__btn-white > .after {
  background-color: #fff;
}

.icon__btn-white:hover {
  background: #fff;
  color: var(--brand-primary);
}

.icon__btn-sm {
  font-size: 14px;
  width: 41px;
  height: 41px;
}

.icon__btn-md {
  font-size: 16px;
  width: 52px;
  height: 52px;
}

.icon__btn-md_1 {
