}

.icon__btn.icon__btn-svg {
  background: transparent !important;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(2);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes pulse-paused {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(1.2);
  }
  60% {
    transform: scale(0.9);
    opacity: 0;
  }
  100% {
    transform: scale(1);
  }
}

@keyframes lightning {
  0% {
    left: -100px;
    z-index: 3;
  }
  50% {
    left: 300%;
  }
  100% {
    left: -100px;
    z-index: 0;
  }
}

@-webkit-keyframes lightning {
  0% {
    left: -100px;
    z-index: 3;
  }
  50% {
    left: 100%;
  }
  100% {
    left: -100px;
    z-index: 0;
  }
}

@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -9999px;
  }
}

